import useTokenSelect from '@/hook/useTokenSelect'
import useTokenSelectStore from '@cetus/stores/src/useTokenSelectStore'
import { Token } from '@cetus/types'
import { Icon } from '@cetus/ui-kit'
import { Flex } from '@chakra-ui/react'
import { useMemo, useState } from 'react'
import SingleTokenInfo from '../SingleTokenInfo'

type CollectListBoxProps = {
  whiteTokenList?: Token[]
  onSelectToken: (tokenInfo: Token) => void
}
export default function CollectListBox(props: CollectListBoxProps) {
  const { onSelectToken, whiteTokenList } = props

  const { delCollectToken } = useTokenSelect()
  const { collectList } = useTokenSelectStore()
  const tokenList = useMemo(() => {
    if (whiteTokenList && whiteTokenList?.length > 0) {
      const whiteCoinTypes = whiteTokenList?.map(item => item.coin_type)
      const list = collectList.filter(ele => whiteCoinTypes.includes(ele.coin_type))
      return list
    }
    return collectList
  }, [whiteTokenList, collectList])
  const [hoverIconIndex, setHoverIconIndex] = useState(-1)

  return (
    <Flex flexWrap="wrap" marginTop="16px">
      {tokenList.map((tokenItem, index) => {
        return (
          <Flex
            key={tokenItem.coin_type}
            w="auto"
            p={{ base: '6px 8px', lg: '6px 12px' }}
            align="center"
            marginRight="8px"
            borderRadius="8px"
            background="bg_secondary"
            borderStyle="solid"
            border="1px"
            borderColor="border"
            marginBottom="8px"
            cursor="pointer"
            position="relative"
            userSelect="none"
            _hover={{
              background: 'primary_opacity.10'
            }}
            onClick={() => onSelectToken(tokenItem)}
            onMouseEnter={() => setHoverIconIndex(index)}
            onMouseLeave={() => setHoverIconIndex(-1)}
          >
            <SingleTokenInfo
              coinType={tokenItem.coin_type}
              imgBoxStyle={{ w: '20px', h: '20px' }}
              symbolFontSize="14px"
              haveName={false}
              warningIcon={{ iconW: '10px', iconH: '10px' }}
            />
            {hoverIconIndex == index && !(tokenItem.suggested_priority && tokenItem.suggested_priority[0]) ? (
              <Icon
                xlinkHref="#icon-icon_close"
                variant="round"
                position="absolute"
                boxW="20px"
                boxH="20px"
                top="-8px"
                right="-8px"
                onClick={e => {
                  e.stopPropagation()
                  delCollectToken(tokenItem)
                }}
              />
            ) : null}
          </Flex>
        )
      })}
    </Flex>
  )
}
